@use "./base.scss";

.p5 {
  text-align: center;
  animation: loading 0.5s linear;
}

@keyframes loading {
  from {
    scale: 0;
    -webkit-transform: scale(0);
  }

  to {
    scale: 1;
    -webkit-transform: scale(1);
  }
}

.sketch-wrapper {
  width: 100%;
  background: #000;
}

.tips {
  display: flex;
  justify-content: center;
  font-size: 36px;
}

.gap-color {
  background: radial-gradient(
    50% 50% at 50% 50%,
    #5f88f0 0%,
    #3d3c33 44.27%,
    #000 100%
  );
  width: 100%;
  height: 64px;
  transform: rotate(-180deg);
}
